import {
    BarChart,
    Bar,
    Cell,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    Legend,
    ResponsiveContainer,
    Area,
    ComposedChart
  } from "recharts";

  

function  BarChartsTwo(props){
    const title = props.title;
    const data = props.data;
    const dataKey1 = props.dataKey1;
    const dataKey2 = props.dataKey2;
    const chartColor1 = props.chartColor1;
    const chartColor2 = props.chartColor2;
    const interval = props.interval;
    const yAxisMax = props.yAxisMax;
    const barSize = props.barSize;

    return (
      
    <ResponsiveContainer width="99%" height="99%">    
        <ComposedChart
          data={data}
          width0={500}
          margin={{
            top: 100,
            right: 70,
            left: 30,
            bottom: 30,
          }} barGap={-barSize}
        >
            <text x={20} y={20} fill="white" textAnchor="left" dominantBaseline="central">
                <tspan fontSize="26" fontWeight={"lighter bold"}>{title}</tspan>
            </text>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey= "Datum" stroke="white" interval={interval}/>
            <YAxis stroke="grey" domain={[0, yAxisMax]}/>
            <Tooltip />
            <Bar dataKey= {dataKey2} stackId= "b" fill= {chartColor2} barSize={barSize}/>
            <Bar dataKey= {dataKey1} stackId= "a" fill= {chartColor1} barSize={barSize}/>

            
        </ComposedChart>
    </ResponsiveContainer>
    )
}

export  default BarChartsTwo;

