import React from "react";
import { Component } from 'react';
import { Responsive, WidthProvider } from "react-grid-layout";
import BarCharts from "../Graphs/BarChart";

//import dataFlowHours from "../Data/Results_Flow_PerHour_2001.json"
//import dataWorkloadHours from "../Data/Results_WorkLoad_PerHour_2001.json"
import BarChartsTwo from "../Graphs/BarChart_two";
import LineCharts from "../Graphs/LineChart";
import * as Sentry from "@sentry/react";

import dataClassifier from "../Data/06.10.22/ClassifierPerDay_2006_06.10.22.json";
import dataFlowHours from "../Data/06.10.22/Results_Flow_PerHour_2006_06.10.22.json"
import dataWorkloadHours from "../Data/06.10.22/Results_WorkLoad_PerHour_2006_06.10.22.json"
const ResponsiveGridLayout = WidthProvider(Responsive);


const chart_Color = "#27DFC0b4";
const chart_Color2= "#1EA8F9b4";
//#DE3136b4
//#1EA8F9 blau
//##367369 dunkel Grün

//throw new Error("ThirdTab.js userName 1001")
export default class ThirdTab extends Component {
    ctx = document.getElementById('divFlowTag');
    constructor() {
        super();
        this.state = {
          value: true,
        };
        this.onHandle = this.onHandle.bind(this);
      }
      onHandle() {
        this.setState((prevState) => ({
          value: !prevState.value,
        }));
      }

      //handleClick(onClick){
      //  const points = divFlowTag.getElementAtEventForMode(click, 'nearest', {intersect: true}, true);
      //  if(points.length=1 ) {
      //      console.log(points[0])
            //divFlowStunde
      //  }
      //  console.log(points.length)

      //}
      
      

      render() {
        var date = Date().toLocaleString()
        Sentry.captureMessage(date + ";Kognitive Tätigkeiten; userName 2006");
        var layout = [
          { i: "a", x: 0, y: 0, w: 1, h: 1},
          { i: "b", x: 1, y: 0, w: 2, h: 1},
          { i: "d", x: 0, y: 1, w: 1, h: 1},
          { i: "e", x: 1, y: 1, w: 2, h: 1},
        ];
    
        var layout = { lg: layout };
        return (
          <div>
            <ResponsiveGridLayout
              className="layout"
              layouts={layout}
              breakpoints={{ lg: 1200, md: 996, sm: 768, xs: 480, xxs: 0 }}
              cols={{ lg: 3, md: 3, sm: 1, xs: 1, xxs: 1 }}
              width={1200}
              rowHeight={350}
              isResizable
              isDraggable={false}
            >
              <div id="divFlowTag" key="a">
                <BarChartsTwo title="Flow pro Tag [min]" data={dataClassifier} dataKey1="Flow [min]" dataKey2="Gesammelte Daten [min]" chartColor1={chart_Color} chartColor2= {chart_Color2} barSize={100}/>
              </div>
              <div id="divFlowStunde" key="b">
                <BarChartsTwo title="Flow pro Stunde [min]" data={dataFlowHours} dataKey1="Flow pro Stunde [min]" dataKey2="Gesammelte Daten [min]" chartColor1={chart_Color} chartColor2= {chart_Color2} interval={11} yAxisMax={60} barSize={10}/>
              </div>
              <div id="divWorkloadTag" key="d">
                <BarChartsTwo title="Hohe mentale Arbeitsbelastung pro Tag [min]" data={dataClassifier} dataKey1="Hohe Arbeitsbelastung [min]" dataKey2="Gesammelte Daten [min]" chartColor1={chart_Color} chartColor2= {chart_Color2} barSize={100} />
              </div>
              <div id="divWorkloadStunde" key="e">
                <BarChartsTwo title="Hohe Arbeitsbelastung pro Stunde [min]" data={dataWorkloadHours} dataKey1="Hohe Arbeitsbelastung pro Stunde [min]" dataKey2="Gesammelte Daten [min]" interval = {11} barSize={10} chartColor1={chart_Color} chartColor2= {chart_Color2} yAxisMax={60}/>
              </div>
            </ResponsiveGridLayout>
          </div>
        );
      }
    }