import React from "react";
import { Component } from 'react';
import { Responsive, WidthProvider } from "react-grid-layout";
import BarCharts from "../Graphs/BarChart";
import LineCharts from "../Graphs/LineChart";
import PieCharts from "../Graphs/PieChart";
import * as Sentry from "@sentry/react";



import dataCollected1 from "../Data/06.10.22/CollectedTasks_2006_06.10.22.json";
import dataHours from "../Data/06.10.22/HoursDataCollection_2006_06.10.22.json"
import dataCollected from "../Data/06.10.22/DataCollected_2006_06.10.22.json"
import dataLabels from "../Data/06.10.22/Labels_2006_06.10.22.json"
const ResponsiveGridLayout = WidthProvider(Responsive);


const dataCollected_Tasks = dataCollected1.CollectedDataTasks;

const chart_Color = "#1C9EEBb4";

export default class FirstTab extends Component {
constructor() {
    super();
    this.state = {
      value: true,
    };
    this.onHandle = this.onHandle.bind(this);
  }
  onHandle() {
    this.setState((prevState) => ({
      value: !prevState.value,
    }));
  }
  render() {
    var date = Date().toLocaleString()
    Sentry.captureMessage(date + ";Übersicht;userName 2006");
    var layout = [
      { i: "a", x: 0, y: 0, w: 3, h: 1},
      { i: "b", x: 3, y: 0, w: 3, h: 1},
      { i: "c", x: 0, y: 0, w: 2, h: 1},
      { i: "d", x: 2, y: 1, w: 2, h: 1},
      { i: "e", x: 4, y: 1, w: 2, h: 1},

    ];

    var layout = { lg: layout };
    
    return (
      <div>
        
        <ResponsiveGridLayout
          className="layout"
          layouts={layout}
          breakpoints={{ lg: 1100, md: 996, sm: 768, xs: 480, xxs: 0 }}
          cols={{ lg: 6, md: 6, sm: 1, xs: 1, xxs: 1 }}
          width={1100}
          rowHeight={350}
          isResizable
          isDraggable={false}
        >
          <div id="div1" key="a" >
            <BarCharts title="Gesamtdauer Sessions [h]" data={dataHours} dataKey="Session-Dauer" chartColor= {chart_Color} yAxisMax={10}/>
          </div>
          <div id="div1" key="b">
            <BarCharts title="Anzahl Sessions" data={dataHours} dataKey="Anzahl Sessions" chartColor={chart_Color} yAxisMax={10}/>
          </div>
          <div id="div1" key="c">
            <BarCharts title="Gesammelte Daten [MB]" data={dataCollected} dataKey="Gesammelte Daten [MB]" chartColor={chart_Color} yAxisMax={1000}/>
          </div>
          <div id="div1" key="d">
            <BarCharts title="Beantwortete Fragebögen" data={dataLabels} dataKey="Anzahl Fragebögen" chartColor={chart_Color} yAxisMax={10}/>
          </div>
          <div id="div1" key="e">
            <PieCharts title="Bearbeitete Aufgaben" data={dataCollected_Tasks} dataKey="Häufigkeit" chartColor={chart_Color}/>
          </div>
        </ResponsiveGridLayout>
      </div>
    );
  }
}